<template>
    <div class="orders">
        <h1 class="title title--primary">Shipping Methods</h1>
        <p class="title title--secondary">New shipping method</p>
        <NewShippingMethodForm @submit="newShippingMethod($event)" />
        <h1 class="title title--primary">Orders</h1>
        <el-table
        :data="tableData"
        striped
        style="width: 100%">
            <el-table-column
            prop="date"
            label="Date"
            width="180">
            </el-table-column>
            <el-table-column
            prop="name"
            label="Name"
            width="180">
            </el-table-column>
            <el-table-column
            prop="address"
            label="Address">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import NewShippingMethodForm from "@/components/NewShippingMethodForm.vue"

    export default {
        name: "orders",
        components: {
            NewShippingMethodForm
        },
        data() {
            return {
                orders: [],
                activeNames: ["1"]
            }
        },
        mounted() {
            this.$emit("set-loading")
            this.$axios({
                method: "get",
                url: "/orders"
            })
            .then((res) => {
                console.log(res)
                this.orders = res.data;
                this.$emit("stop-loading")
            })
            .catch(() => {
                this.$message.error("Error loading orders. Please contact your system administrator")
                this.$emit("stop-loading")
            })
        },
        methods: {
            newShippingMethod(data) {
                this.$axios.post("/orders/methods", data)
                .then(() => {
                    this.$message.success("Successfully created new shipping method")
                })
                .catch(() => {
                    this.$message.error("Error creating new shipping method. Please try again, then contact your system administrator if problem persists.")
                })
            }
        }
    }
</script>